@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&display=swap');
*{
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    font-family: 'Muli', sans-serif;
}
.logo{
    font-size: 22px;
    
}
.nav-item{

    margin-right: 30px;
     font-size: 18px;
    
    
}
.home{
    color: yellow;
    text-decoration: none;
}
.nav-item .item{
    text-decoration: none;
    color:black;
}
.nav-item :hover{
    color:blue;
    
}
.image img{
    width:500px;
}
.image .img-logo{
    /* background-color: red; */
    font-size: 280px;
    color:blue;
    animation: simple 3s  infinite alternate;
}
@keyframes simple {
   0%{
        margin-top: 25px;
        color:gray;
        
   } 
  
   100%{
    margin-bottom: 25px;
    
   }
}
.box{
    
    display: flex;
    justify-content: center;
    align-items: center;

}
strong{
    color:blue;
    font-size: 25px;
    font-weight: bold;

}